.input{font-family:HelveticaNeueBold,sans-serif;font-size:17px;line-height:21px;color:#000;width:100%;min-height:55px;border:2px solid #707070;padding:15px 60px 15px 85px;border-radius:30px;-webkit-transition:all .2s ease-out;-o-transition:all .2s ease-out;transition:all .2s ease-out}.input::-webkit-input-placeholder{font-family:HelveticaNeueBold,sans-serif;font-size:17px;line-height:21px;color:#000}.input:-moz-placeholder,.input::-moz-placeholder{font-family:HelveticaNeueBold,sans-serif;font-size:17px;line-height:21px;color:#000}.input:-ms-input-placeholder{font-family:HelveticaNeueBold,sans-serif;font-size:17px;line-height:21px;color:#000}.input--error{border:2px solid #ff2b25}.input__item{position:relative}.input__icon{position:absolute;top:calc(50% - 2px);-webkit-transform:translateY(-50%);-ms-transform:translateY(-50%);transform:translateY(-50%);fill:#707070}.input__icon--left{left:34px}.input__icon--right{right:34px}