.popup-default{position:relative;font-family:HelveticaNeueBold,sans-serif;font-size:13px;line-height:16px;color:#1a1818;background:#fff;max-width:544px;padding:55px 44px 38px;-webkit-box-shadow:0 3px 6px rgba(0,0,0,.16);box-shadow:0 3px 6px rgba(0,0,0,.16);border-radius:10px;margin:40px auto}.popup-default__close{position:absolute;top:16px;right:18px;width:24px;height:24px;cursor:pointer}.popup-default__close .icon-close{fill:#e71621}.popup-default__title{font-family:HelveticaNeueBold,sans-serif;font-size:30px;line-height:34px;color:#000}.popup-default__title--center{text-align:center}.popup-default__title--offset{margin-bottom:36px}.popup-default__item{margin-bottom:20px}.popup-default__item--offset-large{margin-top:36px;margin-bottom:36px}.popup-default__item--flex{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:justify;-ms-flex-pack:justify;justify-content:space-between;-ms-flex-wrap:wrap;flex-wrap:wrap}.popup-default__link,.popup-default__text{font-family:HelveticaNeueBold,sans-serif;font-size:13px;line-height:16px;color:#1a1818}.popup-default__text--large{font-size:18px;line-height:22px}.popup-default__text--center{text-align:center}.popup-default__text--or{font-size:18px;line-height:22px;text-align:center;margin:16px 0 22px}.popup-default__button--fifty{display:grid;grid-template-columns:repeat(2,1fr);grid-gap:0 30px}